___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 768px) {
  .nav-container {
    padding: 0.7em;
  }

  .image-logo-nav {
    width: 30%;
    height: 7em;
  }

  .hamburger {
    font-size: 2.3em;
    margin-top: 0.4em;
  }

  .drawer {
    width: 40%;
  }

  .drawer.show-drawer {
    transform: translate(7em, 5em);
  }

  .image-logo-foot {
    width: 20%;
  }

  .restaurant-detail {
    margin: 2em auto;
    padding: 1.5em;
    width: 42em;
  }

  .restaurant-detail h2 {
    font-size: 2em;
  }

  .restaurant-detail img {
    max-width: 90%;
  }

  .restaurant-info {
    margin: auto 3em;
  }

  .restaurant-info h3 {
    font-size: 1.7em;
  }

  .restaurant-info p {
    font-size: 1.3em;
  }

  .restaurant-info h4 {
    margin-top: 0.5em;
    font-size: 1.5em;
  }

  .restaurant-menu {
    margin: auto 3em;
  }

  .restaurant-menu h3 {
    font-size: 1.7em;
  }

  .restaurant-menu h4 {
    font-size: 1.5em;
  }

  .restaurant-menu li {
    font-size: 1.3em;
  }

  .review-title {
    font-size: 1.7em;
  }

  .restaurant-reviews {
    margin: 0;
    margin-bottom: 1em;
  }

  .review-card h4,
  span,
  p {
    font-size: 1.2em;
  }

  .hero picture img {
    height: 20em;
  }

  .hero-content {
    padding: 2em;
    margin: 3% 20%;
    font-size: 1.5em;
  }
}

@media screen and (min-width: 1200px) {
  .image-logo-nav {
    width: 15%;
    height: 5em;
  }

  .hamburger {
    display: none;
  }

  .drawer {
    width: auto;
    height: auto;
    position: static;
    transform: none;
    transition: none;
    background-color: transparent;
    margin: auto 0;
  }

  .drawer ul {
    padding: 0;
  }

  .drawer li {
    display: inline;
    margin: 0;
    padding: 0;
  }

  .drawer a {
    padding: 2em;
  }

  .drawer a:hover {
    background-color: #16697a;
  }

  .hero picture img {
    height: 25em;
  }

  .hero-content {
    padding: 2em;
    margin: 3% 20%;
    font-size: 2em;
  }

  .content-title {
    font-size: 2.4em;
  }

  .image-logo-foot {
    width: 13%;
  }

  .restaurant-detail {
    width: 60em;
  }
}
